import React from "react";
import { Select } from "antd";
import { useQuery } from "react-query3";
import { Computer } from "../../types";

export default function ComputerSelectBox(props) {
  const { data, isLoading } = useQuery<Computer[]>("/Computer");
  return (
    <Select {...props} loading={isLoading}>      
      {data?.map((comp) => (
        <Select.Option key={comp.name} value={comp.name}>{comp.name}</Select.Option>
      ))}
    </Select>
  );
}
